<template>
  <div>
    <CCard>
      <CCardBody>
        <h4 style="color:#8c5c7d"><CIcon name="cil-settings" size="lg"/> 設備管理</h4>
        <hr class="mt-0 mb-2">
        <CRow class="pt-3">
          <CCol md="2">
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-right">產線:</label>
              <div class="col-sm-8">
                <multiselect class="mb-3" v-model="selectedLine" :options="optionsLine" @open="getOptionsLine" @select="SelectLine" label="line_name" track-by="line_name" selectLabel="選取" deselectLabel="取消" :searchable="false" placeholder="請選擇"></multiselect>
              </div>
            </div>
          </CCol>
          <CCol md="3">
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">零件名稱:</label>
              <div class="col-sm-8">
                <multiselect v-model="selectedName" :options="optionsName" @open="getOptionsName" @select="onSelectPart" label="part_name" track-by="part_name" selectLabel="選取" deselectLabel="取消" placeholder="輸入幾碼搜尋"></multiselect>
              </div>
            </div>
          </CCol>
          <CCol md="3">
            <CButton color="info" class="mr-2 mb-4" @click="showAddModal"><font-awesome-icon icon="plus"/> 新增</CButton>
          </CCol>
        </CRow>
        <CRow>
          <CCol>
            <h6 class="float-right">資料筆數: <CBadge color="warning">{{items.length}}</CBadge></h6>
          </CCol>
        </CRow>
        <CDataTable
          :hover="hover"
          :striped="striped"
          :bordered="bordered"
          :small="small"
          :fixed="fixed"
          :items="items"
          :fields="fields"
          :items-per-page="small ? 20 : 50"
          :dark="dark"
          class="text-center"
          border
          pagination
        >
          <template #no-items-view>
            <br>
            <h5>
              無資料
            </h5>
            <br>
          </template>
          <template #life_time="{item}">
            <td>
              {{formatHour(item.life_time)}}
            </td>
          </template>
          <template #use_time="{item}">
            <td>
              {{formatHour(item.use_time)}}
            </td>
          </template>
          <template #res_time="{item}">
            <td>
              {{formatHour(item.res_time)}}
            </td>
          </template>
          <template #reset_date="{item}">
            <td>
              {{formatDate(item.reset_date)}}
            </td>
          </template>
          <template #data="{item}">
            <td>
              <CImg :src="item.data" style="width:60px;height:40px;"/>
            </td>
          </template>
          <template #action="{item}">
            <td>
              <CButton type="reset" style="background:#5582BD;color:white" class="mr-1" @click="openModal(item)"><font-awesome-icon icon="pencil-alt"/> 修改</CButton>
              <CButton type="reset" style="background:#AD6560;color:white" class="mr-1" @click="deletePart(item)"><font-awesome-icon icon="trash-alt"/> 刪除</CButton>
            </td>
          </template>
          
        </CDataTable>
        <!-- addModal Component start -->
        <CModal
          :show.sync="addModal"
          :no-close-on-backdrop="true"
          title="新增零件資料"
          borderColor="info"
        >
          <CForm>
            <div class="form-group row mb-3">
              <label class="col-sm-4 col-form-label text-right">產線:</label>
              <div class="col-sm-6">
                <multiselect class="mb-1" v-model="newPart.line" :options="optionsLine" @open="getOptionsLine" label="line_name" selectLabel="選取" deselectLabel="取消" :searchable="false" placeholder="請選擇"></multiselect>
              </div>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">零件名稱</label>
              <div class="col-sm-6">
                <CInput v-model="newPart.part_name" size="lg" placeholder="請輸入"/>
              </div>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">零件編號</label>
              <div class="col-sm-6">
                <CInput v-model="newPart.part_id" size="lg" placeholder="請輸入"/>
              </div>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">使用壽命</label>
              <div class="col-sm-6">
                <CInput v-model="newPart.life_time" size="lg" placeholder="請輸入"/>
              </div>
              <label class="col-sm-2 col-form-label">小時</label>
            </div>
            <div class="form-group row mb-1">
              <div class="col-sm-4 text-right">
                <label class="col-form-label ">零件圖片</label>
              </div>
              <div class="col-sm-6">
                <file-upload
                  input-id="upload"
                  ref="upload"
                  v-model="files"
                  class="btn btn-info"
                  extensions="jpg,jpeg,png,webp"
                  accept="image/png,image/jpeg,image/webp"
                  :multiple=false
                  @input-filter="inputFilter"
                  @input-file="inputFile">
                  選擇圖檔
                </file-upload>
                <!-- <CButton type="reset" color="info" size="sm" @click="openImage"><font-awesome-icon icon="file-alt"/> 開啟圖檔</CButton> -->
                <!-- <CImg :src="addModal.data" style="width:120px;height:80px;"/> -->
              </div>
            </div>
            <div class="form-group row mb-1">
              <div class="col-sm-4 text-right">
                <label class="col-form-label "></label>
              </div>
              <div class="col-sm-6">
                <!-- <CImg :src="newPart.data" style="width:120px;height:80px;"/> -->
                <img id="image" :src="files.length ? files[0].url : imgPart" width="240px" height="160px" />
              </div>
            </div>
          </CForm>
          <template #footer>
            <!-- <CButton @click="Test" color="secondary">+1</CButton> -->
            <CButton @click="addModal=false" color="secondary">取消</CButton>
            <CButton @click="addPart" color="info">新增</CButton>
          </template>
        </CModal>
        <!-- addModal Component end -->

        <!-- editModal Component start -->
        <CModal
          :show.sync="editModal"
          :no-close-on-backdrop="true"
          title="修改零件資料"
          borderColor="info"
        >
          <CForm>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">產線</label>
              <div class="col-sm-6">
                <CInput v-model="editPart.line_id" size="lg" disabled placeholder="請輸入"/>
              </div>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">零件名稱</label>
              <div class="col-sm-6">
                <CInput v-model="editPart.part_name" size="lg" disabled placeholder="請輸入"/>
              </div>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">零件編號</label>
              <div class="col-sm-6">
                <CInput v-model="editPart.part_id" size="lg" placeholder="請輸入"/>
              </div>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">使用壽命</label>
              <div class="col-sm-6">
                <CInput v-model="editPart.life_time" size="lg" placeholder="請輸入"/>
              </div>
              <label class="col-sm-2 col-form-label">小時</label>
            </div>
            <div class="form-group row mb-1">
              <label class="col-sm-4 col-form-label text-right">使用時間</label>
              <div class="col-sm-6">
                <CInput v-model="editPart.use_time" size="lg" placeholder="請輸入"/>
              </div>
              <label class="col-sm-2 col-form-label">小時</label>
            </div>
            <div class="form-group row mb-1">
              <div class="col-sm-4 text-right">
                <label class="col-form-label ">零件圖片</label><br>
                <file-upload
                  input-id="upload1"
                  ref="upload1"
                  v-model="files1"
                  class="btn btn-info mb-1"
                  extensions="jpg,jpeg,png,webp"
                  accept="image/png,image/jpeg,image/webp"
                  :multiple=false
                  @input-filter="inputFilter"
                  @input-file="inputFile">
                  選擇圖檔
                </file-upload>
                <CButton v-if="files1.length" type="reset" color="info" @click="updateImage()">更新圖檔</CButton>
              </div>
              <div class="col-sm-6">
                <CImg :src="files1.length ? files1[0].url : editPart.data" style="width:240px;height:160px;"/>
                <!-- <img id="image" :src="files.length ? files[0].url : imgPart" width="240px" height="160px" /> -->
              </div>
            </div>
          </CForm>
          <template #footer>
            <!-- <CButton @click="Test" color="secondary">+1</CButton> -->
            <CButton @click="editModal=false" color="secondary">取消</CButton>
            <CButton @click="updatePartTime" color="info">重設</CButton>
          </template>
        </CModal>
        <!-- editModal Component end -->
      </CCardBody>
    </CCard> 
  </div>
</template>

<script>
import part from '../../public/img/brand/part.jpg'
import flatpickr from "flatpickr"

export default {
  data() {
    return {
      selectedLine: null,
      optionsLine: [],
      selectedName: null,
      optionsName: [],
      editModal: false,
      editPart: {
        line_id: '',
        part_name: '',
        part_id: '',
        life_time: 0,
        use_time: 0,
        res_time: 0,
        reset_date: '',
        data: ''
      },
      addModal: false,
      newPart: {
        line: '',
        part_name: '',
        part_id: '',
        life_time: 0,
        use_time: 0,
        res_time: 0,
        reset_date: '',
        data: ''
      },
      files: [],
      files1: [],
      base64: '',
      imgPart: part,
      items: [],
      fields: [
        // { key: 'index', label: '項次' },
        { key: 'line_id', label: '產線', _style: 'background:#FFFFF1' },
        { key: 'part_name', label: '零件名稱', _style: 'background:#FFFFF1' },
        { key: 'part_id', label: '零件編號', _style: 'background:#FFFFF1' },
        { key: 'life_time', label: '使用壽命', _style: 'background:#FFFFF1' },
        { key: 'use_time', label: '已使用時間', _style: 'background:#FFFFF1' },
        { key: 'res_time', label: '剩餘時間', _style: 'background:#FFFFF1' },
        { key: 'reset_date', label: '上次重設時間', _style: 'background:#FFFFF1' },
        { key: 'data', label: '零件圖片', _style: 'background:#FFFFF1' },
        { key: 'action', label: '操作', _style: 'background:#FFFFF1' }
      ],
      hover: true,
      striped: true,
      bordered: true,
      small: false,
      fixed: true,
      dark: false,

      selectedLotNo: null,
      selectedSpec: null,
      optionsLotNo: [],
      optionsSpec: [],
    }
  },
  created() {
    // check login user auth.
    // if(sessionStorage.getItem('ROLE')!=='admin') {
    //   this.$router.push('/pages/401')
    //   return
    // }

    // 先取得產線1的零件設備
    this.$http.get('/api/parts/getAllParts', {params: {lineId: 1}})
    .then(res => {
      this.items = res.data
    })
    .catch(err => {
      console.log(`Error: ${err}`)
    })
    
    // 設定下拉選單的item
    this.$http.get('/api/getAllLines')
    .then(res => {
      this.optionsLine = []
      for(let i=0; i<res.data.length; i++) {
        this.optionsLine.push(res.data[i])
      }
      this.selectedLine = this.optionsLine.find(x => x.line_id==='1')
    })
    .catch(err => {
      console.log(`Error: ${err}`)
    })
  },
  methods: {
    getOptionsLine() {
      this.$http.get('/api/getAllLines')
      .then(res => {
        this.optionsLine = []
        for(let i=0; i<res.data.length; i++) {
          this.optionsLine.push(res.data[i])
        }
      })
      .catch(err => {
        console.log(`Error: ${err}`)
      })
    },
    getOptionsName() {
      let param = {
        lineId: this.selectedLine ? this.selectedLine.line_id : null
      }
      this.$http.get('/api/parts/getDistinctPartsName', {params: param})
      .then(res => {
        this.optionsName = res.data
      })
      .catch(err => {
        console.log(`Error: ${err}`)
      })
    },
    SelectLine(selectedItem) {
      // this.$http.get('/api/parts/getAllParts', {params: {lineId: selectedItem.line_id}})
      // .then(res => {
      //   this.items = res.data
      // })
      // .catch(err => {
      //   console.log(`Error: ${err}`)
      // })
      let param = {
        lineId: selectedItem.line_id,
        partName: this.selectedName ? this.selectedName.part_name : null
      }

      this.$http.get('/api/parts/getPartData', {params: param})
      .then(res => {
        this.items = res.data
      })
      .catch(err => {
        console.log(`Error: ${err}`)
      })
    },
    onSelectPart(selectedName) {
      let param = {
        lineId: this.selectedLine ? this.selectedLine.line_id : null,
        partName: selectedName.part_name
      }

      this.$http.get('/api/parts/getPartData', {params: param})
      .then(res => {
        this.items = res.data
      })
      .catch(err => {
        console.log(`Error: ${err}`)
      })
    },
    openModal(item) {
      this.editPart.line_id = item.line_id
      this.editPart.part_name = item.part_name
      this.editPart.part_id = item.part_id
      this.editPart.life_time = item.life_time
      this.editPart.use_time = item.use_time
      this.editPart.res_time = item.res_time
      this.editPart.reset_date = item.reset_date
      this.editPart.data = item.data
      this.files1.length = 0
      this.editModal = true
    },
    deletePart(item) {
      if(confirm('確定要將零件:'+item.part_name+'，這筆資料刪除?')){
        let obj = {lineId: item.line_id, partName: item.part_name, file: item.part_pic_file}
        this.$http.post('/api/parts/deletePart', {data: obj})
        .then(res => {
          // 刪除表格中該筆資料
          let idx = this.items.findIndex(x => x.line_id===item.line_id && x.part_name===item.part_name)
          this.items.splice(idx, 1)
          alert(`${res.data.msg}`)
        })
        .catch(err => {
          console.log(`Error: ${err}`)
        })
      }
    },
    showAddModal() {
      this.newPart.line = null
      this.newPart.part_name = ''
      this.newPart.part_id = ''
      this.newPart.life_time = ''
      this.files = []
      this.addModal = true
    },
    addPart() {
      if(!this.newPart.line) {
        alert('請選擇產線')
        return
      }
      if(!this.newPart.part_name) {
        alert('請輸入零件名稱')
        return
      }
      if(!this.newPart.life_time) {
        alert('請輸入使用壽命')
        return
      }

      const file = this.files[0] && this.files[0].file

      if(!file) {
        console.log('請選擇圖檔')
        return
      }
      
      let self = this
      var reader = new FileReader()
      reader.readAsDataURL(file) 
      reader.onloadend = function() {
        var base64data = reader.result                

        const formData = new FormData()
        formData.append('data', base64data)
        formData.append('line_id', self.newPart.line.line_id)
        formData.append('part_name', self.newPart.part_name)
        formData.append('part_id', self.newPart.part_id)
        formData.append('life_time', self.newPart.life_time)


        var contenttype = {
          headers: { 'content-type': 'multipart/form-data' }
        }

        /* 上傳檔案 */
        self.$http.post('/api/parts/insertPart', formData, contenttype)
        .then(res => {
          let param = {
            lineId: self.newPart.line.line_id,
            partName: self.newPart.part_name
          }

          self.$http.get('/api/parts/getPartData', {params: param})
          .then(result => {
            if(result.data.length>0) {
              self.items.splice(0, 0, result.data[0])
            }
          })
          .catch(err => {
            console.log(`Error: ${err}`)
          })
          console.log(`${res.data.msg}`)
        })
        .catch(err => {
          console.log(`${err}`)
        })
      }
      
      this.addModal = false
    },
    inputFile(newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        this.$nextTick(function () {
          this.edit = true
        })
      }
      if (!newFile && oldFile) {
        this.edit = false
      }
    },
    inputFilter(newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        if (!/\.(gif|jpg|jpeg|png|webp)$/i.test(newFile.name)) {
          this.alert('Your choice is not a picture')
          return prevent()
        }
      }
      if (newFile && (!oldFile || newFile.file !== oldFile.file)) {
        newFile.url = ''
        let URL = window.URL || window.webkitURL
        if (URL && URL.createObjectURL) {
          newFile.url = URL.createObjectURL(newFile.file)
        }
      }
    },
    updateImage() {
      const file = this.files1[0] && this.files1[0].file
      if(!file) {
        console.log('請選擇圖檔')
        return
      }

      let self = this
      var reader = new FileReader()
      reader.readAsDataURL(file) 
      reader.onloadend = function() {
        var base64data = reader.result                

        const formData = new FormData()
        formData.append('data', base64data)
        formData.append('line_id', self.editPart.line_id)
        formData.append('part_name', self.editPart.part_name)

        var contenttype = {
          headers: { 'content-type': 'multipart/form-data' }
        }

        /* 上傳檔案 */
        self.$http.post('/api/parts/updateImage', formData, contenttype)
        .then(res => {
          let param = {
            lineId: self.editPart.line_id,
            partName: self.editPart.part_name
          }

          let idx = self.items.findIndex(x => x.line_id===self.editPart.line_id && x.part_name===self.editPart.part_name)

          self.$http.get('/api/parts/getPartData', {params: param})   // 重新取得資料
          .then(result => {
            if(result.data.length>0) {
              self.items.splice(idx, 1, result.data[0])   // 從位置idx刪除1筆資料，然後插入一筆資料(result.data[0])
            }
          })
          .catch(err => {
            console.log(`Error: ${err}`)
          })
          console.log(`${res.data.msg}`)
        })
        .catch(err => {
          console.log(`${err}`)
        })
      }
    },
    updatePartTime() {
      if(!this.editPart.life_time) {
        alert('請輸入零件的使用壽命')
        return
      }
      if(!this.editPart.use_time) {
        this.editPart.use_time = 0
      }

      let data = {
        lineId: this.editPart.line_id,
        partName: this.editPart.part_name,
        partId: this.editPart.part_id,
        lifeTime: this.editPart.life_time,
        useTime: this.editPart.use_time
      }

      this.$http.post('/api/parts/updatePartData', {data: data})
      .then(res => {
        // update table data
        let idx = this.items.findIndex(x => x.line_id===this.editPart.line_id && x.part_name===this.editPart.part_name)
        this.items[idx].part_id = this.editPart.part_id
        this.items[idx].life_time = this.editPart.life_time
        this.items[idx].use_time = this.editPart.use_time
        this.items[idx].res_time = this.items[idx].life_time - this.items[idx].use_time
        this.items[idx].reset_date = flatpickr.formatDate(new Date(), "Y/m/d")
        alert('重設成功')
      })
      .catch(err => {
        console.log(err)
      })

      this.editModal = false
    },
    formatHour(hour) {
      if(!hour) {
        return hour
      }
      let resHour = hour % 24  // hour       
      let resDay = Math.floor(hour / 24)   // day
      return resDay + '天' + resHour + '小時' 
    },
    formatDate(date) {
      let dt = date ? flatpickr.formatDate(new Date(date), "Y/m/d") : ''
      return dt
    },
  }
}
</script>
